}

.service__item {
  counter-increment: my-counter;
  width: 450px;
  box-shadow: 0 0 0 1px #c4c4c4;
  padding: 12px 19px 9px 113px;
  position: relative;
  transition: box-shadow 0.3s, width 0.3s ease-in-out;
