}

.service__item:not(:last-child) {
  margin-bottom: 12px;
}

.service__item:hover {
  width: 500px;
  box-shadow: 0 0 0 2px #5a0d67;
}

.service__item:hover::before {
  color: #5a0d67;
}

.service__item:hover .service__item-title {
  color: #5a0d67;
}

.service__item-title {
  font-size: 24px;
  font-weight: 300;
  line-height: 27px;
  transition: color 0.3s ease-in-out;
}

.service__item-text {
  line-height: 27px;
}

.service__btn {
  padding: 18px 54.5px;
}

.service__btn--decore {
  position: relative;
}

.service__btn--decore::after {
  position: absolute;
  content: "жми";
  text-transform: uppercase;
  color: #c4c4c4;
  margin-left: 139px;
}

.service__btn--decore::before {
  position: absolute;
  content: "";
  width: 80px;
  height: 2px;
  background-color: #c4c4c4;
  right: -75px;
  top: 50%;
}

.benefits {
  padding-bottom: 100px;
}

.benefits__inner {
