@media (max-width: 1130) {
  body {
    font-size: calc(16px + 3.4px * ((100vw - 320px) / 1230));
  }

  .title {
    font-size: calc(36px + 61.2px * ((100vw - 320px) / 1230));
  }

  .subtitle {
    font-size: calc(23px + 83.3px * ((100vw - 320px) / 1230));
  }
}